* {
  font-family: 'Tahoma';
}

@media (min-width: 600px) {
  .MuiToolbar-regular {
    min-height: 48px !important;
  }
}

.gradient {
  background: radial-gradient(#525252, #000000);
  height: 100vh;
  color: '#fff'
}

.nav-wrapper {
  background-color: #000000;
}

.slick-websites {
  width: 100%;
}

.slick-websites .slick-slide,
.slick-websites .slick-cloned,
.carouesl-home .slick-slide,
.carouesl-home .slick-cloned {
  display: flex !important;
  justify-content: center;
  align-items: center;
}

.slick-websites .slick-list {
  height: 75px !important;
  margin: auto;
}

.slick-websites .slick-track {
  height: 100%;
}


#root {
  position: relative;
  min-height: 100vh;
}

main {
  min-height: 100vh
}